body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  width: 100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
    background-color: #fafafa;
    height: 100vh;
    width: 100vw;
}
.calc-container{
    height: 667px;
    width: 375px;
    overflow: hidden;
    --current-operation-height: 30%;
}

@media only screen and (max-width : 600px) {
    .calc-container{
        height: 100vh;
        width: 100vw;
    }
}

.current-operation{
    height: var(--current-operation-height);
    background-color: #fafafa;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    position: relative;
    padding: 10px;
    display: flex;
    overflow: hidden;
}

.current-operation .expression{
    font-size: 4em;
    position: absolute;
    right: 20px;
    top: 30%;
    -webkit-transform-origin: right;
            transform-origin: right;
}

.current-operation.computed .expression{
    transition: all 300ms cubic-bezier(0.4, 0.0, 1, 1);
    -webkit-transform: translateY(-200%);
            transform: translateY(-200%);
    opacity: 0;
}

.current-operation .result{
    font-size: 2em;
    color: #5a5a5a;
    position: absolute;
    right: 20px;
    top: 70%;
    -webkit-transform-origin: right;
            transform-origin: right;
}

.current-operation.computed .result{
    transition: all 300ms cubic-bezier(0.4, 0.0, 0.2, 1);
    -webkit-transform: translateY(-170%) scale(2);
            transform: translateY(-170%) scale(2);
    color: inherit;
}


.buttons-container{
    background-color: #434343;
    height: calc(100% - var(--current-operation-height));
}

.buttons-container > * {
    height: 25%;
}

.buttons-container .button{
    height: 100%;
    width: 100%;
    color: #fafafa;
    border-radius: 0;
    font-size: 1.3em;
}

.buttons-container .button:hover{
    background-color: #5a5a5a;
}

.buttons-container .operation-button{
    background-color: #636363;
}

.buttons-container .operation-button:hover{
    background-color: #6e6e6e;
}
